import './FlareTx-Report-Section-Container-Component.scss';
import React, { Component } from 'react';
import { HashLink } from 'react-router-hash-link';
import {REPORT_SUBSECTIONS} from '../../../Constants';


class FlareTxReportSectionContainerComponent extends Component {
    
    render() {

        const subsections = []
        Object.keys(REPORT_SUBSECTIONS).forEach ((key) => {
            subsections.push(key)
        })

        let counter = 0;
        const displaySections = subsections.map (subsection => {
            const hashLink = `#${subsection}`
            counter++;
            return (
                <>
                    <HashLink smooth to={hashLink} className="hashlink-component">
                        <div>
                            <p>{counter}. {REPORT_SUBSECTIONS[subsection]}</p>
                        </div>
                    </HashLink>
                </>

                
            );
        });

        return(
            <div>
                <div className="report-container__header">
                    <p>Report Sections</p>
                </div>
                <div className="report-container__list d-flex flex-column">
                    { displaySections }
                </div>  
            </div>
        );
    };
}

export default FlareTxReportSectionContainerComponent;
