import './FlareTx-TF-Container-Component.scss';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';


class FlareTxTFContainerComponent extends Component {
    render() {

        const DisplayTF = this.props.tfList.map( factor => {
            let path = `/${factor}`
            return(
                    <Link to={{
                                pathname: path,
                                state: {
                                    expanded: true
                                }
                            }} 
                            key={factor} 
                            className="link-component"> 
                            <div className="factor-list">{factor}</div>
                    </Link>
            );
        });


        return(
            <div>
                <div className="tf-container__header">
                    <p>Factors({this.props.size})</p>
                </div>
                <div className="tf-container__list d-flex flex-column">
                    { DisplayTF }
                </div>  
            </div>
        );
    };
}

export default FlareTxTFContainerComponent;
