import './FlareTx-SideDrawer-Component.scss';
import axios from 'axios';
import React, {Component} from 'react';
import FlareTxFiltersComponent from './FlareTx-Filters-Component/FlareTx-Filters-Component';
import FlareTxTFContainerComponent from './FlareTx-TF-Container-Component/FlareTx-TF-Container-Component';
import FlareTxReportSectionContainerComponent from './FlareTx-Report-Section-Container-Component/FlareTx-Report-Section-Container-Component';

class FlaretxSideDrawerComponent extends Component {

    state = {
        transcriptionFactorList: [],
        displayedTF: [],
        url: 'https://ap-south-1.aws.webhooks.mongodb-realm.com/api/client/v2.0/app/application-0-jhiwj/service/getAllFactors/incoming_webhook/all_factors_api',
        expanded: this.props.expanded
    };

    componentDidMount() {
        axios.get(this.state.url)
        .then((response) =>{
            let tfList = []
            Object.keys(response.data).forEach((object) =>{
                Object.keys(response.data[object]).forEach((key) => {
                    tfList.push(key)
                })
            })
            this.setState({
                transcriptionFactorList: tfList,
                displayedTF: tfList
            });
        })
    }

    searchHandler = (event) => {
        let searcjQery = event.target.value.toLowerCase(),
            displayedContacts = this.state.transcriptionFactorList.filter((el) => {
              let searchValue = el.toLowerCase();
              return searchValue.indexOf(searcjQery) !== -1;
            })
        this.setState({
            displayedTF: displayedContacts
        })
    }

    render() {
        return(
            <div className={this.props.expanded ? "sidebar sidebar__expanded" : "sidebar"}> 
            {this.props.expanded ? <>
                                <FlareTxFiltersComponent toggle={this.props.toggle} searchHandler={this.searchHandler}/>
                                <div className="d-flex flex-row">
                                    <div className="tf-container">
                                        <FlareTxTFContainerComponent 
                                        size={this.state.displayedTF.length} 
                                        tfList={this.state.displayedTF} 
                                        expanded={this.state.expanded}/>
                                    </div>
                                    <div className="report-container"> 
                                        <FlareTxReportSectionContainerComponent/>
                                    </div>
                                </div>
                              </> 
                            : <></>}
            </div>
        );
    }
}

export default FlaretxSideDrawerComponent;