import './FlareTx-ExternalLinksList-Component.scss';
import React, { Component } from 'react';

class FlareTxExternalLinksListComponent extends Component {

    render() {

        let counter = 0;
        const externalLinksList = this.props.value.map(link => {
            let keys = []
            Object.keys(link).forEach((key) =>{
                keys.push(key)
            })
            return (
                <div key={this.props.name}>
                    {
                        keys.map(key => {
                            counter++;
                            return (
                                <>
                                    <a className="links" href={link[key]}>
                                        {counter}. {key}&emsp;
                                        <i className="fa fa-external-link links__icon"></i>
                                    </a>
                                </>
                            );
                        })
                    }
                </div>
            )
        });

        return(
            <>
                <div className="external-link-component">
                    <div>{this.props.name}</div>
                    <div>
                        {externalLinksList}
                    </div>
                </div>
            </>
        );
    }
}

export default FlareTxExternalLinksListComponent;