import './App.css';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import React, { Component } from 'react';
import FlaretxSideDrawerComponent from './Components/FlareTx-SideDrawer-Component/FlareTx-SideDrawer-Component';
import FlareTxInformationComponent from './Components/FlareTx-Information-Component/FlareTx-Information-Component';
import FlareTxLogoComponent from './Components/FlareTx-Logo-Component/FlareTx-Logo-Component';
import FlareTxHomePageComponent from './Components/FlareTx-HomePage-Component/FlareTx-HomePage-Component';
import FlareTxErrorPageComponent from './Components/FlareTx-ErrorPage-Component/FlareTx-ErrorPage-Component';

class App extends Component {

  state = {
    expanded: true  
  }

  toggleSidebar = () => {
      this.setState({
      expanded: !this.state.expanded
    })
  }

  render()  {
    return (
      <div>
        <BrowserRouter basename="test-mvp/">
          <FlareTxLogoComponent />
          <FlaretxSideDrawerComponent expanded={this.state.expanded} toggle={this.toggleSidebar}/>
          <Switch>
            <Route path="/" exact component={() => <FlareTxHomePageComponent expanded={this.state.expanded} />}  />
            <Route path="/:transcriptionFactor" exact component={() => <FlareTxInformationComponent expanded = {this.state.expanded} toggle={this.toggleSidebar}/>} />
            <Route path="*" component={() => <FlareTxErrorPageComponent expanded = {this.state.expanded}/>}/>
          </Switch> 
        </BrowserRouter>
      </div>
    );
  } 
}

export default App;