import './FlareTx-Table-Component.scss';
import React, { Component } from 'react';

class FlareTxTableComponent extends Component {
    
    render() {
        if (this.props.value.length === 0) {
            return(<></>);
        }
        
        let keys = [];
        Object.keys(this.props.value[0]).forEach((key) => {
            keys.push(key);
        });

        const tableHead = keys.map(key => {
            return (
                <th key={key}>{key}</th>
            );
        })

        let counter = 0;
        const tableBody = this.props.value.map((object) => {
            counter++;
            return(  
                <tr key={counter}> 
                {
                    keys.map(key => {
                        counter++;
                        return (
                            <td key={counter}>{object[key]}</td>
                        )
                    })
                }
                </tr>
            );
        })

        return(
            <table className="table-component">
                <caption>{this.props.name}</caption>
                <thead>
                    <tr>
                        {tableHead}
                    </tr>
                </thead>
                <tbody>
                    {tableBody}
                </tbody>
            </table>
        );
    };
}

export default FlareTxTableComponent;
