import React, { Component } from 'react';
import './FlareTx-Information-Section-Component.scss';
import FlareTxExternalLinkListComponent 
    from '../../FlareTx-Utility-Components/FlareTx-ExternalLinksList-Component/Flaretx-ExternalLinksList-Component';
import FlareTxKeyValueComponent 
    from '../../FlareTx-Utility-Components/FlareTx-KeyValue-Component/FlareTx-KeyValue-Component';
import FlareTxLinkComponent 
    from '../../FlareTx-Utility-Components/FlareTx-Link-Component/FlareTx-Link-Component';
import FlareTxPlotlyComponent 
    from '../../FlareTx-Utility-Components/FlareTx-Plotly-Component/FlareTx-Plotly-Component';
import FlareTxTableComponent 
    from '../../FlareTx-Utility-Components/FlareTx-Table-Component/FlareTx-Table-Component';

const RenderKeyData = (props) => {

    let keys = []

    Object.keys(props.data).forEach((key) => {
        keys.push(key.toLowerCase());
    });
    if (!keys.includes("name") || !keys.includes("datatype") || !keys.includes("value")) {
        return (
            <>
                <div>error</div>
            </>
        );
    }
    if (props.data.dataType === "keyValue") {
        return (
            <FlareTxKeyValueComponent name={props.data.name} value={props.data.value}/>
        );
    } else if (props.data.dataType === "externalLink") {
        return (
            <FlareTxLinkComponent name={props.data.name} value={props.data.value}/>
        );
    }  else if (props.data.dataType === "table") {
        return(
            <FlareTxTableComponent name={props.data.name} value={props.data.value} />
        );     
    }else if (props.data.dataType === "plotlyObject") {
        return (
            <FlareTxPlotlyComponent name={props.data.name} value={props.data.value}/>
        );
    }else if(props.data.dataType === "extenalLinksList") {
        return(
            <FlareTxExternalLinkListComponent name={props.data.name} value={props.data.value}/>
        );
    }else {
        return (
            <>
                <div>Data Type not accepted</div>
            </>
        );
    }
}

const RenderSectionData = (props) => {
    
    let ignoredKeys = [];
    let keyArray = [];

    Object.keys(props.section).forEach((key) => {
        if (key === 'header' || key === 'order') {
            ignoredKeys.push(key);
        } else {
            keyArray.push(key);
        }
    });

    let columnOrientation = false;
    const sectionData = keyArray.map(key => {
        if (props.section[key].dataType === 'table'
            || props.section[key].dataType === 'plotlyObject' 
            || props.section[key].dataType === 'extenalLinksList') {
            columnOrientation = true;
        }
        return (
            <RenderKeyData key={key} data={props.section[key]}/>
        );
    })

    return(
        <>
            <div className={columnOrientation? "d-flex d-column" : "d-flex d-row"}>
                <div className={columnOrientation? "" : "d-flex p-2"}>
                    {sectionData}
                </div>    
            </div>
        </>              
    );
}

class FlareTxInformationSectionComponent extends Component {
    render() {
        return(
            <>
                <div className="section-component">
                    <p className="section-component__heading">{this.props.section.order.$numberInt}. {this.props.section.header}</p>
                    <RenderSectionData section={this.props.section}/>
                </div>
            </>
        );
    }
}

export default FlareTxInformationSectionComponent;