import axios from 'axios';
import React, { Component } from 'react';
import Plot from 'react-plotly.js';

class FlareTxPlotlyComponent extends Component {

    state = {
        url: this.props.value,
        response: {},
        dataNotFound: false
    }

    componentDidMount() {
        axios.get(this.state.url)
        .then(response => {
            if (!response.data) {
                this.setState({
                    dataNotFound: true
                })
            } else {
                this.setState({
                    response: response.data,
                });
            }
        });
    }

    render() {
        return (
            <Plot
                data={this.state.response.data}
                layout={this.state.response.layout}
                config = {{
                    scrollZoom: true,
                    responsive: true,
                }}
            />
        );
  }
}

export default FlareTxPlotlyComponent;