import './FlareTx-Information-Component.scss';
import { useParams } from 'react-router';
import FlareTxInformationHeaderComponent from './FlareTx-Information-Header-Component/FlareTx-Information-Header-Component';
import FlareTxInformationBodyComponent from './FlareTx-Information-Body-Component/FlareTx-Information-Body-Component';
import React from 'react';

const FlareTxInformationComponent = (props) => {

    let { transcriptionFactor } = useParams();
    return(
        <div className={props.expanded ? "main-panel__expanded" : "main-panel"}>
            <div className="main-panel__header">
                <FlareTxInformationHeaderComponent tfName={transcriptionFactor}/>
            </div>
            <div className="d-flex flex-row updated-date-component"> 
                <div className="d-flex">
                    {!props.expanded ? <>
                                        <div className="updated-date-component__open-arrow">
                                            <button onClick={props.toggle}><i className="fa fa-caret-right fa-xs"></i></button>
                                        </div>
                                    </> 
                                    : <></>}
                </div>
                <div className="d-flex updated-date-component__date">
                    Updated on : 
                </div> 
            </div>
            <div className="main-panel__body" key={transcriptionFactor}>
                <FlareTxInformationBodyComponent transcriptionFactor={transcriptionFactor} section={window.location.hash}/>
            </div>
        </div>
    );
}

export default FlareTxInformationComponent;