import './FlareTx-Information-Body-Component.scss'
import axios from 'axios';
import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import FlareTxInformationSectionComponent from '../FlareTx-Information-Section-Component/FlareTx-Information-Section-Component';
import FlareTxErrorPageComponent from '../../FlareTx-ErrorPage-Component/FlareTx-ErrorPage-Component';

class FlareTxInformationBodyComponent extends Component{

    state = {
        data: [],
        dataNotFound: false,
        url: `https://ap-south-1.aws.webhooks.mongodb-realm.com/api/client/v2.0/app/application-0-jhiwj/service/getFactor/incoming_webhook/factor_api?arg1=${this.props.transcriptionFactor}`
    }

    componentDidUpdate() {
        if (!this.props.section)
                return
            let hash_location = this.props.section.substring(1, this.props.section.length);
            let node = ReactDOM.findDOMNode(this.refs[hash_location]);
            if (node) {
                node.scrollIntoView({
                    block: 'start', 
                    behavior: 'smooth',
                });
            }
      }

    componentDidMount() {
        axios.get(this.state.url)
        .then(response => {
            if (response.data.length === 0) {
                this.setState({
                    dataNotFound: true
                })
            } else {
                this.setState({
                    data: response.data,
                });
            }
        });        
    }
    
    render() {
        
        if (this.state.data.length === 0 && this.state.dataNotFound) {
            return (
                <FlareTxErrorPageComponent/>
            );
        }

        let keyArray = [];
        this.state.data.forEach((dat) => {
            Object.keys(dat).forEach((key) => {
                if (key !== "_id" && key !== "Family" && key !== "Ratings")
                    keyArray.push(key)
            });
        });

        const individualSection = keyArray.map(key => {
           return (
               <>
                <div>
                    <hr className="body-component__horizontalLine"></hr>
                    <div className="scroll-component body-component__content">
                        <FlareTxInformationSectionComponent ref={key} key={key} section={this.state.data[0][key]} sectionName={key}/>
                    </div>
                </div>
               </>
           )
        })
        return(
            <>
                <div className="body-component scroll-component">
                    {individualSection}
                </div>
            </>              
        );
    }
}

export default FlareTxInformationBodyComponent;